import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { DATES, InputGroupInline, WindowComponent, WindowComponentProps } from 'summer';
import { hasAdministradoraMHO } from '../../../../../dependencies/administradoraMHODependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import EventoMHOIS from '../../../../components/mho/eventoMHOIS/EventoMHOIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, REPORT_EVENT, REPORT_TITLE, TotalFaturamentoEventoRV } from './TotalFaturamentoEventoRV';

const DEFAULT_VALUES: TotalFaturamentoEventoRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  eventos: [],
  eventosAbertos: false,
  eventosBaixados: false,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, DATES.value),
  dtFinalRequired: true,
  dtInicialRequired: true,
  exibirConfiguracaoDadosEmpresa: false,
};

const TotalFaturamentoEventoReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const { handleSubmit, ...formProps } = useForm<TotalFaturamentoEventoRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const validateExtras = (formData: TotalFaturamentoEventoRV): string[] => {
    const errors = [];
    if (formData.dtInicial > formData.dtFinal) {
      errors.push('A data inicial não pode ser maior ou igual a data final.');
    }
    return errors;
  };

  const handleChange = e => {
    if (e.target.id === 'todos') {
      e.target.value = true;
      formProps.setValue('eventosBaixados', false);
      formProps.setValue('eventosAbertos', false);
    } else if (e.target.id === 'eventosAbertos') {
      formProps.setValue('eventosAbertos', true);
      formProps.setValue('eventosBaixados', false);
    } else {
      formProps.setValue('eventosAbertos', false);
      formProps.setValue('eventosBaixados', true);
    }
    formProps.triggerValidation('eventosAbertos');
    formProps.triggerValidation('eventosBaixados');
  };

  return (
    <WindowComponent
      title="Total de Faturamento por Evento"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasAdministradoraMHO(globalReducer.globalParameter)]}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      >
        <InputGroupInline formProps={formProps} id="eventosBaixados" hidden={true} />
        <InputGroupInline formProps={formProps} id="eventosAbertos" hidden={true} />

        <EventoMHOIS formProps={formProps} id="eventos" inputSearchProps={{ selectProps: { isMulti: true } }} />

        <Row>
          <Col>
            <CustomInput type="radio" id="todos" label="Imprimir todos" name="todos" value={'true'} onChange={handleChange} />
          </Col>
          <Col>
            <CustomInput
              type="radio"
              id="eventosBaixados"
              label="Imprimir somente baixados"
              name="todos"
              value={'false'}
              innerRef={formProps.register}
              onChange={handleChange}
            />
          </Col>
          <Col>
            <CustomInput
              type="radio"
              id="eventosAbertos"
              label="Imprimir somente não baixados"
              name="todos"
              value={'false'}
              innerRef={formProps.register}
              onChange={handleChange}
            />
          </Col>
        </Row>
      </ReportForm>
    </WindowComponent>
  );
};

export default TotalFaturamentoEventoReportView;
