import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType, SearchExpression } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mho/eventoMHO';

const EventoMHOIS: FC<InputProps> = props => {
  function getSearchExpression(): SearchExpression[] {
    return [
      {
        alias: 'c',
        conditionalGroup: 'c_',
        fieldName: 'finalidade',
        value: 'manual',
      },
    ];
  }

  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
          {
            dataField: 'tipo',
            text: 'Tipo',
          },
          {
            dataField: 'valor',
            text: 'Valor',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchExpressions: getSearchExpression,
        searchViewId: 'EventoMHOSearchView',
      }}
      label={props.label ? props.label : 'Evento'}
      placeholder={props.placeholder ? props.placeholder : 'Informe o Evento'}
      type={InputType.INPUT_SEARCH}
    />
  );
};
export default EventoMHOIS;
